* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Press Start 2P', cursive;
  background-color: black;
}

.App {
  text-align: center;
  margin: 2rem 0;
}

.App>textarea {
  margin: 2rem 0;
  resize: none;
  color: rgb(39, 38, 38);
  font-size: 20px;
  font-weight: 900;
  padding: 10px;
  background-color: rgb(15, 216, 15);
  outline: none;
  cursor: default;
  caret-color: rgb(255, 0, 0);

}

.App>h1 {
  color: rgb(15, 216, 15);
  font-weight: 900;
}

.App>h4 {
  margin-bottom: 1rem;
  font-size: 25px;
  color: rgb(15, 216, 15);
  font-weight: 900;
}

.App>button {
  background-color: rgb(15, 216, 15);
  color: black;
  padding: 5px 45px;
  font-weight: 900;
  font-size: 25px;
  margin: 30px 0;
  cursor: pointer;
}

.App>button:hover {
  background-color: rgb(13, 141, 13);
}

.App>button:disabled {
  cursor: not-allowed;
}

textarea:disabled {
  background-color: gray;
}

#noOfSec {
  margin-bottom: 1.5rem;
  padding: 0.5rem 0.3rem;
  text-align: center;
  color: rgb(255 253 253);
  outline: none;
  border: 2px solid rgb(13, 141, 13);
  font-weight: 900;
  /* background-color: rgb(13, 141, 13); */
  font-size: 17px;
  margin-right: 3rem;
}

label {
  color: white;
  margin-right: 1rem;
  font-size: 20px;
}